body {
  background-color: #151515;
  font-size: 16px;
}

main {
  overflow: hidden;
}

.container {
  margin: 0 16px;
}

.breakline {
  height: 1px;
  background-color: #ffffff;
}

@media screen and (min-width: 768px) {
  .container {
    margin: 0 30px;
  }
  .breakline__skill {
    display: none;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    margin: 0 165px;
  }
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Space Grotesk", sans-serif;
  font-weight: 500;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.about {
  padding: 20px 0px 80px 0px;
}
.about .header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 32px;
  gap: 20px;
}
.about .header__heading {
  font-size: 24px;
  color: #ffffff;
}
.about .header__icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 154px;
}
.about .header__icon {
  display: flex;
  align-items: center;
  width: 24px;
  height: 24px;
  color: #ffffff;
}
.about .header__icon:hover {
  color: #4ee1a0;
}
.about__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
}
.about__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about__image:hover {
  border: 5px solid #4ee1a0;
  opacity: 1;
}
.about__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 343px;
  color: #ffffff;
  margin: 0 auto;
  gap: 24px;
}
.about__heading {
  font-size: 40px;
  line-height: 40px;
}
.about__name {
  text-decoration: underline;
  text-decoration-color: #4ee1a0;
}
.about__description {
  color: rgba(217, 217, 217, 0.8509803922);
  line-height: 26px;
}
.about__button {
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  padding: 20px 40px;
  color: #ffffff;
  background-color: #151515;
  font-weight: 700;
  letter-spacing: 2.6px;
}
.about__button:hover {
  color: #4ee1a0;
}
.about__button::after {
  position: absolute;
  content: "";
  width: 115px;
  left: 50%;
  height: 2px;
  bottom: 10px;
  transform: translate(-50%);
  background-color: #4ee1a0;
}
.about__picture {
  margin-bottom: 40px;
  width: 300px;
  z-index: 1;
}
.about__img--fourcircles {
  position: absolute;
  left: -50%;
  top: 150px;
}
.about__img--whitecircle {
  position: absolute;
  top: 300px;
  right: -100px;
}
.about__link {
  color: #ffffff;
}
.about__link:hover {
  color: #4ee1a0;
}

@media screen and (min-width: 768px) {
  .about__content {
    position: relative;
  }
  .about__content .header {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 90px;
  }
  .about__box {
    flex-direction: row-reverse;
    gap: 25px;
  }
  .about__picture {
    margin-bottom: 0px;
    align-items: center;
    min-width: 320px;
  }
  .about__image {
    border: none;
  }
  .about__title {
    position: relative;
    align-items: flex-start;
    text-align: left;
  }
  .about__heading {
    font-size: 72px;
    line-height: 88px;
  }
  .about__description {
    font-size: 18px;
    line-height: 28px;
  }
  .about__button {
    padding: 20px 0px;
  }
  .about__img--fourcircles {
    position: absolute;
    left: -50%;
    top: 50px;
  }
  .about__img--whitecircle {
    display: none;
  }
}
@media screen and (min-width: 1440px) {
  .about {
    padding: 39px 0px 104px 0px;
  }
  .about__heading {
    font-size: 88px;
  }
  .about__img--fourcircles {
    position: absolute;
    left: -22.5%;
    top: 50px;
  }
  .about__img--whitecircle {
    display: block;
    top: 80%;
    right: 23%;
    z-index: 9999;
  }
}
.skill {
  color: #ffffff;
  padding: 40px 0;
}
.skill__content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px;
}
.skill__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.skill__description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  gap: 1px;
  width: 345px;
}
.skill__description:not(:last-child) {
  margin-bottom: 24px;
}
.skill__language {
  font-size: 32px;
}
.skill__experience {
  font-size: 18px;
  color: rgba(217, 217, 217, 0.8509803922);
}
.skill__img {
  position: absolute;
  bottom: -75px;
  left: 50%;
}

@media screen and (min-width: 768px) {
  .skill {
    padding: 52px 0px 100px 0px;
  }
  .skill__box {
    flex-direction: row;
    justify-content: center;
    margin-bottom: 52px;
    gap: 10px;
    width: 100%;
  }
  .skill__description {
    align-items: flex-start;
    gap: 14px;
  }
  .skill__description:not(:last-child) {
    margin-bottom: 0px;
  }
  .skill__language {
    font-size: 48px;
  }
  .skill__img {
    position: absolute;
    bottom: -25px;
    left: 75%;
  }
}
@media screen and (min-width: 1440px) {
  .skill__content {
    flex-direction: row;
  }
  .skill__box {
    flex-direction: column;
  }
  .skill__img {
    position: absolute;
    bottom: -10px;
    left: 90%;
  }
}
.project__content {
  padding: 80px 0;
}
.project__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.project__image {
  margin-bottom: 20px;
}
.project__heading {
  font-size: 40px;
  color: #ffffff;
}
.project__buttons {
  display: flex;
  align-items: center;
  gap: 30px;
}
.project__button {
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  color: #ffffff;
  background-color: #151515;
  font-weight: 700;
  letter-spacing: 2.6px;
}
.project__button:hover {
  color: #4ee1a0;
}
.project__button--contact::after {
  position: absolute;
  content: "";
  left: 50%;
  height: 2px;
  bottom: -10px;
  transform: translate(-50%);
  background-color: #4ee1a0;
  width: 115px;
}
.project__button--project::after {
  position: absolute;
  content: "";
  left: 50%;
  height: 2px;
  bottom: -10px;
  transform: translate(-50%);
  background-color: #4ee1a0;
  width: 130px;
}
.project__button--code::after {
  position: absolute;
  content: "";
  left: 50%;
  height: 2px;
  bottom: -10px;
  transform: translate(-50%);
  background-color: #4ee1a0;
  width: 100px;
}
.project__box {
  margin-bottom: 40px;
}
.project__box--last {
  margin-bottom: 0px;
}
.project__name {
  font-size: 24px;
  color: #ffffff;
  line-height: 32px;
  margin-bottom: 7px;
}
.project__skills {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 18px;
  margin-bottom: 20px;
}
.project__skill {
  font-size: 18px;
  color: rgba(217, 217, 217, 0.8509803922);
}
.project__overlay {
  display: none;
}
.project__link {
  color: #ffffff;
}
.project__link:hover {
  color: #4ee1a0;
}

@media screen and (min-width: 768px) {
  .project {
    position: relative;
  }
  .project__title {
    margin-bottom: 60px;
  }
  .project__element {
    display: grid;
    gap: 22px;
    grid-template-columns: 1fr 1fr;
  }
  .project__heading {
    font-size: 72px;
  }
}
@media screen and (min-width: 1440px) {
  .project__buttons {
    display: none;
  }
  .project__box {
    position: relative;
  }
  .project__box:hover .project__image {
    opacity: 0.3;
  }
  .project__box:hover .project__overlay {
    opacity: 1;
  }
  .project__image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: 0.5s ease;
    backface-visibility: hidden;
  }
  .project__overlay {
    display: flex;
    transition: 0.5s ease;
    display: block;
    position: absolute;
    height: auto;
    top: 45%;
    left: 50%;
    transition: 0.5s ease;
    opacity: 0;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }
  .project__overlay--buttons {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }
  .project__overlay--button {
    background-color: inherit;
    color: #ffffff;
    border: none;
    cursor: pointer;
  }
  .project__overlay--button:hover {
    color: #4ee1a0;
  }
  .project__overlay--viewproject {
    font-size: 18px;
    text-transform: uppercase;
  }
  .project__overlay--viewcode {
    font-size: 18px;
    text-transform: uppercase;
  }
  .project__overlay--viewcode::after {
    position: absolute;
    content: "";
    left: 50%;
    height: 2px;
    bottom: -10px;
    transform: translate(-50%);
    background-color: #4ee1a0;
    width: 100px;
  }
  .project__overlay--viewproject::after {
    position: absolute;
    content: "";
    width: 140px;
    left: 50%;
    height: 2px;
    bottom: 60px;
    transform: translate(-50%);
    background-color: #4ee1a0;
  }
  .project__overlay--link {
    color: #ffffff;
  }
  .project__overlay--link:hover {
    color: #4ee1a0;
  }
}
.contact {
  background-color: #242424;
  position: relative;
}
.contact__content {
  padding: 60px 0px 87px 0px;
}
.contact__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 343px;
  margin-bottom: 50px;
}
.contact__heading {
  font-size: 40px;
  color: #ffffff;
  margin-bottom: 20px;
}
.contact__text {
  color: rgba(217, 217, 217, 0.8509803922);
}
.contact__fields {
  display: flex;
  flex-direction: column;
}
.contact__inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.contact__box {
  position: relative;
}
.contact__input {
  background-color: #242424;
  width: 100%;
}
.contact__input--name {
  padding: 10px 35px;
  border: none;
  color: #ffffff;
  border-bottom: 1px solid rgba(217, 217, 217, 0.8509803922);
  outline: none;
  cursor: pointer;
}
.contact__input--email {
  padding: 10px 35px;
  border: none;
  color: #ffffff;
  border-bottom: 1px solid rgba(217, 217, 217, 0.8509803922);
  outline: none;
  cursor: pointer;
}
.contact__input::placeholder {
  color: rgba(217, 217, 217, 0.8509803922);
}
.contact__input:focus {
  border-bottom: 1px solid red;
}
.contact__message {
  border-bottom: 1px solid #ffffff;
  margin-bottom: 32px;
}
.contact__textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  height: 100px;
  outline: none;
  color: #ffffff;
  background-color: #242424;
  border-style: none;
}
.contact__textarea::placeholder {
  color: rgba(217, 217, 217, 0.8509803922);
}
.contact__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.contact__button {
  color: #ffffff;
  outline: none;
  border: none;
  background-color: #242424;
  cursor: pointer;
}
.contact__button:hover {
  color: #4ee1a0;
}
.contact__button::after {
  position: absolute;
  content: "";
  width: 106px;
  right: -53px;
  height: 2px;
  bottom: -10px;
  transform: translate(-50%);
  background-color: #4ee1a0;
}
.contact__img {
  position: absolute;
  bottom: 70px;
  right: 50%;
  width: 530px;
  height: 129px;
}
.contact__icon {
  position: absolute;
  top: 5px;
  color: #ffffff;
  width: 24px;
  height: 24px;
}
.contact__faliure, .contact__succes {
  right: 0;
  opacity: 0;
}
.contact__faliure, .contact__error {
  color: red;
}
.contact__succes {
  color: #4ee1a0;
}
.contact__error {
  font-size: 16px;
  margin-top: 2px;
}

@media screen and (min-width: 768px) {
  .contact__content {
    display: flex;
    flex-direction: column;
    padding: 60px 0px 92px;
    margin: 0 162px;
  }
  .contact__heading {
    font-size: 72px;
  }
  .contact__text {
    font-size: 18px;
  }
  .contact__img {
    position: absolute;
    bottom: 15px;
    right: 77.5%;
  }
}
@media screen and (min-width: 1440px) {
  .contact__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 220px;
  }
  .contact__title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    margin-bottom: 110px;
  }
  .contact__heading {
    font-size: 88px;
    margin-bottom: 0;
  }
  .contact__img {
    bottom: 40px;
    right: 77.5%;
  }
}
.footer {
  background-color: #242424;
}
.footer__content {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 39px 0px 60px 0px;
}
.footer__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.footer__heading {
  font-size: 24px;
  color: #ffffff;
}
.footer__icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 154px;
}
.footer__icon {
  display: flex;
  align-items: center;
  width: 24px;
  height: 24px;
  color: #ffffff;
}
.footer__icon:hover {
  color: #4ee1a0;
}

@media screen and (min-width: 768px) {
  .footer__box {
    flex-direction: row;
    justify-content: space-between;
    gap: 0;
    width: 100%;
  }
  .footer__heading {
    font-size: 32px;
  }
}